body {
	margin: 0;
	padding: 0;
	font-family: 'Lato', sans-serif;
	background: #7F7FD5;  
	background: -webkit-linear-gradient(to right, #91EAE4, #86A8E7, #7F7FD5);
	background: -webkit-gradient(linear, left top, right top, from(#91EAE4), color-stop(#86A8E7), to(#7F7FD5));
	background: -webkit-linear-gradient(left, #91EAE4, #86A8E7, #7F7FD5);
	background: -o-linear-gradient(left, #91EAE4, #86A8E7, #7F7FD5);
	background: linear-gradient(to right, #91EAE4, #86A8E7, #7F7FD5);
}



